<template>
  <div class="settings-page" @click="handlePageClick">
    <!-- 顶部导航栏 -->
    <header class="header-bar">
      <button class="back-btn" @click="goBack">
        <svg viewBox="0 0 24 24">
          <path d="M15.41,16.58L10.83,12L15.41,7.41L14,6L8,12L14,18L15.41,16.58Z"/>
        </svg>
      </button>
      <h1 class="header-title">设置</h1>
      <div class="header-placeholder"></div>
    </header>

    <!-- 设置列表 -->
    <div class="settings-list">
      <div class="setting-item" @click="showWallpaperDialog">
        <div class="setting-left">
          <i class="icon icon-wallpaper"></i>
          <span>壁纸设置</span>
        </div>
        <div class="setting-right">
          <span class="current-wallpaper" v-if="$globalConfig.currentWallpaper">已设置</span>
          <i class="arrow"></i>
        </div>
      </div>
      <div class="setting-item" @click="showSecretEntry = true" v-if="$globalConfig.secretPassword">
        <div class="setting-left">
          <i class="icon icon-invite"></i>
          <span>邀请码</span>
        </div>
        <div class="setting-right">
          <i class="arrow"></i>
        </div>
      </div>
      <div class="setting-item" @click="showAboutDialog">
        <div class="setting-left">
          <i class="icon icon-about"></i>
          <span>关于我们</span>
        </div>
        <i class="arrow"></i>
      </div>
    </div>

    <!-- 壁纸设置弹框 -->
    <div v-if="showWallpaper" class="dialog-mask" @click="hideWallpaperDialog">
      <div class="wallpaper-dialog" @click.stop>
        <div class="dialog-header">
          <h3>选择壁纸</h3>
          <i class="close-icon" @click="hideWallpaperDialog">×</i>
        </div>
        <div class="wallpaper-tabs">
          <button 
            class="tab-btn" 
            :class="{ 'active': activeTab === 'preset' }"
            @click="activeTab = 'preset'"
          >
            预设壁纸
          </button>
          <button 
            class="tab-btn" 
            :class="{ 'active': activeTab === 'custom' }"
            @click="activeTab = 'custom'"
          >
            自定义壁纸
          </button>
        </div>
        
        <!-- 预设壁纸 -->
        <div v-show="activeTab === 'preset'" class="wallpaper-container">
          <div 
            v-for="(wallpaper, index) in wallpapers" 
            :key="index"
            class="wallpaper-item"
            :class="{ 'selected': selectedWallpaper === wallpaper.path }"
            @click="selectWallpaper(wallpaper.path)"
          >
            <img :src="wallpaper.path" :alt="wallpaper.name">
            <div class="wallpaper-name">{{ wallpaper.name }}</div>
            <div class="checkmark" v-if="selectedWallpaper === wallpaper.path">✓</div>
          </div>
        </div>
        
        <!-- 自定义壁纸 -->
        <div v-show="activeTab === 'custom'" class="custom-wallpaper-container">
          <div class="url-input-container">
            <input 
              type="text" 
              v-model="customWallpaperUrl" 
              placeholder="输入壁纸网络地址" 
              class="url-input"
              @keyup.enter="addCustomWallpaper"
            >
            <button class="add-btn" @click="addCustomWallpaper">添加</button>
          </div>
          <div class="wallpaper-container">
            <div 
              v-for="(wallpaper, index) in customWallpapers" 
              :key="'custom-'+index"
              class="wallpaper-item"
              :class="{ 'selected': selectedWallpaper === wallpaper }"
              @click="selectWallpaper(wallpaper)"
            >
              <img :src="wallpaper" alt="自定义壁纸">
              <div class="checkmark" v-if="selectedWallpaper === wallpaper">✓</div>
              <button class="delete-btn" @click.stop="removeCustomWallpaper(index)">×</button>
            </div>
          </div>
        </div>
        
        <div class="dialog-footer">
          <button class="cancel-btn" @click="hideWallpaperDialog">取消</button>
          <button class="confirm-btn" @click="confirmWallpaper">确定</button>
        </div>
      </div>
    </div>

    <!-- 关于我们弹框 -->
    <div v-if="showAbout" class="dialog-mask" @click="hideAboutDialog">
      <div class="about-dialog" @click.stop>
        <div class="dialog-header">
          <h3>关于我们</h3>
          <i class="close-icon" @click="hideAboutDialog">×</i>
        </div>
        <div class="about-content">
          <div class="logo-container">
            <img src="@/assets/img/user.jpg" class="app-logo" alt="App Logo">
            <div class="logo-glow"></div>
          </div>
          <h4>李沐沐</h4>
          <p class="copyright">咸鱼 limumu</p>
        </div>
        <div class="dialog-footer">
          <button class="confirm-btn" @click="hideAboutDialog">我知道了</button>
        </div>
      </div>
    </div>

    <!-- 私密入口弹框 -->
    <div v-if="showSecretEntry" class="dialog-mask" @click="hideSecretEntryDialog">
      <div class="secret-entry-dialog" @click.stop>
        <div class="dialog-header">
          <h3>私密入口</h3>
          <i class="close-icon" @click="hideSecretEntryDialog">×</i>
        </div>
        <div class="secret-content">
          <input 
            type="password" 
            maxlength="9"
            v-model="secretPassword" 
            placeholder="请输入密码" 
            class="password-input"
          >
        </div>
        <div class="dialog-footer">
          <button class="cancel-btn" @click="hideSecretEntryDialog">取消</button>
          <button class="confirm-btn" @click="hideSecretEntrySub">确定</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 导入本地壁纸图片
import wallpaper1 from '@/assets/bag/bg1.jpg';
import wallpaper2 from '@/assets/bag/bg2.jpg';
import wallpaper3 from '@/assets/bag/bg3.jpg';
import wallpaper4 from '@/assets/bag/bg4.jpg';

export default {
  name: 'setting',
  data() {
    return {
      showWallpaper: false,
      showAbout: false,
      showSecretEntry: false,
      selectedWallpaper: this.$globalConfig.currentWallpaper || '',
      activeTab: 'preset', // 'preset' 或 'custom'
      customWallpaperUrl: '',
      wallpapers: [
        { name: '蜡笔小新', path: wallpaper1 },
        { name: '动画小镇', path: wallpaper2 },
        { name: '美味甜点', path: wallpaper3 },
        { name: '土拨鼠', path: wallpaper4 }
      ],
      customWallpapers: JSON.parse(localStorage.getItem('customWallpapers') || '[]'),
      secretPassword: '',
      tapSequence: [],
      secretTapPattern: ['left', 'left', 'right', 'right', 'right'], // 左2右3
    }
  },
  methods: {
    goBack() {
      this.$goBackOrHome()
    },
    showWallpaperDialog() {
      this.showWallpaper = true
      this.selectedWallpaper = this.$globalConfig.currentWallpaper || ''
    },
    hideWallpaperDialog() {
      this.showWallpaper = false
      this.customWallpaperUrl = ''
    },
    showAboutDialog() {
      this.showAbout = true
    },
    hideAboutDialog() {
      this.showAbout = false
    },
    selectWallpaper(wallpaper) {
      this.selectedWallpaper = wallpaper
    },
    confirmWallpaper() {
      var that = this
      this.$globalConfig.currentWallpaper = this.selectedWallpaper
      var Wallpapers = JSON.parse(JSON.stringify(this.selectedWallpaper))
      this.wallpapers.forEach(function(item,index){
        if(item.path == that.selectedWallpaper) {
          Wallpapers = index
        }
      })
      localStorage.setItem('Wallpapers', Wallpapers)
      this.hideWallpaperDialog()
    },
    addCustomWallpaper() {
      if (!this.customWallpaperUrl.trim()) return
      
      // 简单验证URL格式
      try {
        new URL(this.customWallpaperUrl)
      } catch (e) {
        alert('请输入有效的URL地址')
        return
      }
      
      // 避免重复添加
      if (!this.customWallpapers.includes(this.customWallpaperUrl)) {
        this.customWallpapers.unshift(this.customWallpaperUrl)
        localStorage.setItem('customWallpapers', JSON.stringify(this.customWallpapers))
      }
      
      this.selectedWallpaper = this.customWallpaperUrl
      this.customWallpaperUrl = ''
    },
    removeCustomWallpaper(index) {
      this.customWallpapers.splice(index, 1)
      localStorage.setItem('customWallpapers', JSON.stringify(this.customWallpapers))
      
      // 如果删除的是当前选中的壁纸，则取消选择
      if (this.selectedWallpaper === this.customWallpapers[index]) {
        this.selectedWallpaper = ''
      }
    },
    // 处理点击事件
    handlePageClick(e) {
      // 如果已经有弹框显示，则不处理
      if (this.showWallpaper || this.showAbout || this.showSecretEntry) return;
      
      const clickType = e.clientX < window.innerWidth / 2 ? 'left' : 'right';
      this.tapSequence.push(clickType);
      
      // 保持序列长度不超过模式长度
      if (this.tapSequence.length > this.secretTapPattern.length) {
        this.tapSequence.shift();
      }
      
      // 检查是否匹配模式
      if (this.arraysEqual(this.tapSequence, this.secretTapPattern)) {
        this.showSecretEntryDialog();
        this.tapSequence = []; // 重置序列
      }
    },
    
    // 比较数组是否相等
    arraysEqual(a, b) {
      if (a.length !== b.length) return false;
      for (let i = 0; i < a.length; i++) {
        if (a[i] !== b[i]) return false;
      }
      return true;
    },
    
    // 显示授权码入口弹框
    showSecretEntryDialog() {
      this.showSecretEntry = true;
    },
    
    // 隐藏授权码弹框
    hideSecretEntryDialog() {
      this.showSecretEntry = false;
    },
    
    // 确定保存
    hideSecretEntrySub() {
      this.showSecretEntry = false;
      this.$globalConfig.secretPassword = this.secretPassword
      localStorage.setItem('secretPassword',this.secretPassword)
    }
  }
}
</script>

